<template>
  <div class="infinite-list-wrapper" style="overflow:auto; height: 597px">
    <el-scrollbar style="height:100%;" class="scrollbar">
      <div
        v-infinite-scroll="load"
        infinite-scroll-disabled="disabled"
        infinite-scroll-distance="20"
      >
        <el-backtop target=".el-scrollbar__wrap" :bottom="80" :right="650"></el-backtop>
        <div>
          <el-card
            v-for="(i, index) in contentData"
            :key="index"
            class="infinite-list-item box-card"
            shadow="hover"
          >
            <div slot="header" class="clearfix">
              <span
                class="title"
                style="font-weight: bold; font-size: 1.2em; font-family: 'pingfang'; cursor:pointer"
                @click="toReading(i.id);"
              >{{ i.content_title }}</span>
              <el-button
                style="float: right; padding: 3px 0; font-weight: bold; font-size: 1.2em; font-family: 'pingfang'; color:#e8bf9d"
                type="text"
              >有用 {{ i.likes }}</el-button>
            </div>
            <div class="text item" @click="toReading(i.id)">{{ i.content + "......." }}</div>
          </el-card>
        </div>
      </div>
      <p v-if="loading" style="text-align:center"><i class="el-icon-loading"></i>加载中...</p>
      <p v-if="noMore" style="text-align:center">没有更多了哦...</p>
    </el-scrollbar>
  </div>
</template>

<script>
import functionList from "./../assets/utility.js";
import store from "@/components/store";
function articleToData(curArticle) {
  // console.log(curArticle);
  var curContent = curArticle.content.replace(/<p>/g, "");
  var curArticleObj = {
    id: curArticle.articleID,
    content_title: curArticle.articleID + curArticle.title,
    likes: curArticle.likes,
    content: curContent,
    dateCreated: curArticle.dateCreated
  };
  return curArticleObj;
}

export default {
  name: "list",
  data() {
    return {
      contentData: store.contentData,
      activeIndex: "/learnCausually",
      msg: "Welcome to Your Vue.js App",
      textarea: "",
      loading: false,
      newlyAdded: 4,
      isEnd: false,
      firstTime: true,
      listLoading: localStorage.getItem("listLoading"),
      linkToFetch: "getAllArticles"
    };
  },

  computed: {
    noMore() {
      return this.isEnd;
    },
    disabled() {
      return this.loading || this.noMore;
    }
  },
  created() {
    this.firstTime = true;
    this.loading = false;
    var articleType = localStorage.getItem("curArticleType");
    if (localStorage.getItem("getLikedArticle") == "true") {
      this.linkToFetch = "getLikedArticle";
    }
    store.contentData = [];
    this.contentData = [];
    store.count = 4;
    console.log("it works here");

    //var curCookieInfo = functionList.checkForCookie();
    // console.log(curCookieInfo)
    functionList.fetchToBackEnd(
      "POST",
      "articles/" + this.linkToFetch,
      result => {
        var articleList = result.articleList;
        for (var i = 0; i < articleList.length; i++) {
          var curArticle = articleList[i];
          var curArticleObj = articleToData(curArticle);
          this.contentData.push(curArticleObj);
        }
        store.count += 4;
        this.isEnd = result.isEnd;
      },
      nr => {
        this.$message({
          message: nr.message,
          type: "warning"
        });
      },
      JSON.stringify({
        start: store.count - this.newlyAdded,
        end: store.count,
        total: store.count,
        articleType: articleType
      })
      // ,
      // curCookieInfo
    );
  },
  methods: {
    load() {
      if (this.firstTime) {
        this.firstTime = false;
      } else {
        var articleType = localStorage.getItem("curArticleType");
        this.loading = true;
        setTimeout(() => {
          //var curCookieInfo = functionList.checkForCookie();

          functionList.fetchToBackEnd(
            "POST",
            "articles/" + this.linkToFetch,
            result => {
              var articleList = result.articleList;
              for (var i = 0; i < articleList.length; i++) {
                var curArticle = articleList[i];
                var curArticleObj = articleToData(curArticle);
                this.contentData.push(curArticleObj);
              }
              this.isEnd = result.isEnd;
              store.count += 4;
              this.loading = false;
            },
            nr => {
              this.$message({
                message: nr.message,
                type: "warning"
              });
            },
            JSON.stringify({
              start: store.count - this.newlyAdded,
              end: store.count,
              total: store.count,
              articleType: articleType
            })
            // ,
            // curCookieInfo
          );
        }, 1000);
      }
    },

    triggerChange(newDataContent){
      // this.contentData = newDataContent;
      // store.contentData = newDataContent
      // console.log(newDataContent);
    },
    
    toReading(articleID) {
      localStorage.setItem("articleID", articleID);
      // console.log(articleID);
      // console.log(localStorage.getItem("articleID"));
      const reading = this.$router.resolve({
        name: "reading",
        params: { articleID: articleID }
      });
      window.open(reading.href, "_blank");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@font-face {
  font-family: "pingfang";
  src: url("../assets/fonts/pingfang.ttf");
}

.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
.text {
  font-size: 14px;
  font-family: "pingfang";
  cursor: pointer;
  transition: color 0.2s;
}
.text:hover {
  color: #a0a0a0;
  transition: color 0.2s;
}
.item {
  margin-bottom: 18px;
  font-size: 1.1em;
}
.title {
  transition: color 0.2s;
}
.title:hover {
  color: #a0a0a0;
  transition: color 0.2s;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: calc(100% - 2px);
  margin-bottom: 12px;
}
.te >>> textarea {
  font-family: "pingfang";
  font-size: 1.5em;
}
.te >>> textarea::-webkit-input-placeholder {
  color: #cdcdcd;
}
.scrollbar >>> .el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>


